import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
// swal
import swal from 'sweetalert';
// @mui
import {
    Box,
    Card,
    Grid,
    Alert,
    Stack,
    Typography,
    Switch,
    FormControlLabel,
    CircularProgress,
    Chip,
} from '@mui/material';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------
VirtualAccountLock.propTypes = {
    setting: PropTypes.object,
};

export default function VirtualAccountLock({ setting }) {
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);
    const [locks, setLocks] = useState([]);
    const [error, setError] = useState(null);

    const AccessToken = window.localStorage.getItem('accessToken');

    // Provider-Account combinations
    const providerAccounts = [
        { provider: 'xixapay', account_type: 'palmpay', label: 'Xixapay (Palmpay)', description: 'Palmpay virtual accounts via Xixapay' },
        { provider: 'xixapay', account_type: 'kolomonie', label: 'Xixapay (Kolomonie)', description: 'Kolomonie virtual accounts via Xixapay' },
        { provider: 'monnify', account_type: 'moniepoint', label: 'Monnify (Moniepoint)', description: 'Moniepoint/Sterling accounts via Monnify' },
        { provider: 'paystack', account_type: 'wema', label: 'Paystack (Wema)', description: 'Dedicated Wema Bank account via Paystack' },
        { provider: 'paymentpoint', account_type: 'palmpay', label: 'PaymentPoint (Palmpay)', description: 'Palmpay virtual accounts via PaymentPoint' },
    ];

    // Fetch current lock status
    useEffect(() => {
        fetchLocks();
    }, []);

    const fetchLocks = async () => {
        try {
            setLoading(true);
            const response = await axios.get(`/api/secure/virtualaccounts/locks/${AccessToken}/habukhan/secure`);
            if (response.data.status === 'success') {
                setLocks(response.data.locks);
            }
        } catch (err) {
            console.error('Error fetching virtual account locks:', err);
            setError('Failed to load virtual account lock settings');
        } finally {
            setLoading(false);
        }
    };

    const getLockStatus = (provider, accountType) => {
        const lock = locks.find(l => l.provider === provider && l.account_type === accountType);
        // Ensure we handle both booleans and numeric strings "0"/"1" from PHP/MySQL
        return lock ? (lock.is_locked === true || lock.is_locked == 1) : false;
    };

    const handleToggle = async (provider, accountType, label) => {
        const currentlyLocked = getLockStatus(provider, accountType);
        const newLockedStatus = !currentlyLocked;

        // Check if trying to lock all providers
        const unlockedCount = locks.filter(l => !l.is_locked).length;
        if (newLockedStatus && unlockedCount === 1) {
            swal('Error', 'Cannot lock all providers. At least one must remain unlocked.', 'error');
            return;
        }

        try {
            setSaving(true);
            const response = await axios.post(`/api/secure/virtualaccounts/locks/toggle/${AccessToken}/habukhan/secure`, {
                provider,
                account_type: accountType,
            });

            if (response.data.status === 'success') {
                // Refresh locks
                await fetchLocks();
                swal('Success', response.data.message, 'success');
            }
        } catch (err) {
            console.error('Error toggling lock:', err);
            swal('Error', err.response?.data?.message || 'Failed to update lock status', 'error');
        } finally {
            setSaving(false);
        }
    };

    if (loading) {
        return (
            <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
                <CircularProgress />
            </Box>
        );
    }

    return (
        <Grid container spacing={3}>
            <Grid item xs={12} md={10}>
                {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}

                <Card sx={{ p: 3 }}>
                    <Typography variant="h6" gutterBottom>
                        Virtual Account Provider Lock/Unlock
                    </Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                        Control which virtual account providers are available to users. Lock providers that are down or experiencing issues. At least one provider must remain unlocked.
                    </Typography>

                    <Stack spacing={2.5}>
                        {providerAccounts.map(({ provider, account_type, label, description }) => {
                            const isLocked = getLockStatus(provider, account_type);
                            return (
                                <FormControlLabel
                                    key={`${provider}_${account_type}`}
                                    control={
                                        <Switch
                                            checked={!isLocked}
                                            onChange={() => handleToggle(provider, account_type, label)}
                                            disabled={saving}
                                            color="primary"
                                        />
                                    }
                                    label={
                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                            <Box sx={{ flex: 1 }}>
                                                <Typography variant="subtitle1">{label}</Typography>
                                                <Typography variant="caption" color="text.secondary">
                                                    {description}
                                                </Typography>
                                            </Box>
                                            <Chip
                                                label={isLocked ? 'Locked' : 'Active'}
                                                color={isLocked ? 'error' : 'success'}
                                                size="small"
                                                variant="outlined"
                                            />
                                        </Box>
                                    }
                                />
                            );
                        })}
                    </Stack>

                    <Alert severity="info" sx={{ mt: 3 }}>
                        <Typography variant="body2">
                            <strong>Note:</strong> Changes take effect immediately. Locked providers will not appear in the Fund Wallet screen. Users will only see unlocked virtual account options.
                        </Typography>
                    </Alert>

                    <Alert severity="warning" sx={{ mt: 2 }}>
                        <Typography variant="body2">
                            <strong>Tip:</strong> Both Xixapay and PaymentPoint offer Palmpay accounts. If one provider is down, you can lock it and keep the other active as a backup.
                        </Typography>
                    </Alert>
                </Card>
            </Grid>
        </Grid>
    );
}
